import React, { useState } from "react";
import { Link } from 'react-router-dom';

import userIcon from '../../assets/images/user-icon3.svg';
import passwordIcon from '../../assets/images/password-icon.svg';
import './Signup.css';

const Signup = () => {

    const [inputValues, setInputValues] = useState({
        login: '',
        password: '',
        name: '',
        email: '',
        phone: '',
        sex: -1,
        birthDay: '',
        birthMonth: -1,
        birthYear: '' });

    const onLoginChange = (event) => {
        const login = event.target.value;
        setInputValues({ ...inputValues, login: login })
    }

    const onPasswordChange = (event) => {
        const password = event.target.value;
        setInputValues({ ...inputValues, password: password })
    }

    const onNameChange = (event) => {
        const name = event.target.value;
        setInputValues({ ...inputValues, name: name })
    }

    const onEmailChange = (event) => {
        const email = event.target.value;
        setInputValues({ ...inputValues, email: email })
    }

    const onPhoneChange = (event) => {
        const phone = event.target.value;
        setInputValues({ ...inputValues, phone: phone })
    }

    const setSex = (sexNum) => {
        setInputValues({ ...inputValues, sex: sexNum })
    }

    const [selectedType, setSelectedType] = useState(-1);

    let maleRadio = "radio relative pointer"
    if (inputValues.sex === 0) {
        maleRadio = "radio selected-radio-m relative pointer"
    }
    let femaleRadio = "radio relative pointer"
    if (inputValues.sex === 1) {
        femaleRadio = "radio selected-radio-f relative pointer"
    }

    return (
        <div className="signup container">
            <div className="container">
                <div className="sign-selection border-radius text-centered pointer" onClick={() => setSelectedType(0)}>
                    Я родитель или ребенок
                </div>
                { 
                    selectedType === 0 ? <div>
                        <div className="sign-selection border-radius text-centered pointer" onClick={() => setSelectedType(1)}>
                            Я родитель, регистрирую ребенка на курс и себя как наблюдателя
                        </div>
                        <div className="sign-selection border-radius text-centered pointer" onClick={() => setSelectedType(2)}>
                            Я родитель, хочу наблюдать за существующем на курсе ребенком
                        </div>
                        <div className="sign-selection border-radius text-centered pointer" onClick={() => setSelectedType(3)}>
                            Я ребенок
                        </div>
                    </div> : <div className="hide" />
                }
                <div className="sign-selection border-radius text-centered pointer" onClick={() => setSelectedType(4)}>
                    Я взрослый или студент
                </div>
                <div className="sign-selection border-radius text-centered pointer" onClick={() => setSelectedType(5)}>
                    Я хочу преподавать
                </div>
                {
                    selectedType > 0 ? <div className="container shadow form-cnt border-radius">
                        <h2>Регистрация</h2>
                        {/* Все данные являются обязательными для заполнения */}


                        <div className="flex-column input-cnt relative">
                            
                            <input type="text" id="login" className="inputs i-p inputs-width" onChange={onLoginChange} required/>
                            <label htmlFor="login" className="for-input">Логин</label>
                            {/* <img src={userIcon} /> */}
                        </div>

                        <div className="flex-column input-cnt relative">
                            
                            <input type="password" id="password" className="inputs i-b inputs-width" onChange={onPasswordChange} required/>
                            <label htmlFor="password" className="for-input">Пароль</label>
                            {/* <img src={passwordIcon} /> */}
                        </div>

                        <div className="flex-column input-cnt relative">
                            <input type="text" id="name" className="inputs i-p inputs-width" onChange={onNameChange} required/>
                            <label htmlFor="name" className="for-input">ФИО</label>
                        </div>

                        <div className="flex-column input-cnt relative">
                            <input type="text" id="email" className="inputs i-b inputs-width" onChange={onEmailChange} required/>
                            <label htmlFor="email" className="for-input">Электронная почта</label>
                        </div>

                        <div className="flex-column input-cnt relative">
                            <input type="tel" id="tel" className="inputs i-p inputs-width" onChange={onPasswordChange} required/>
                            <label htmlFor="tel" className="for-input">Номер телефона</label>
                        </div>

                        <div className="flex input-cnt relative inputs-width">
                            <div style={{marginRight: 20}} className="flex">
                                {/* <input id="male" name="button" type="radio" className="radio" /> */}
                                <div id="male" className={maleRadio} style={{marginRight: 5}}  onClick={() => setSex(0)} />
                                <label className="pointer" onClick={() => setSex(0)}>Мужчина</label>
                            </div>
                            <div style={{marginRight: 20}} className="flex">
                                <div id="female" className={femaleRadio} style={{marginRight: 5}} onClick={() => setSex(1)} />
                                <label className="pointer" onClick={() => setSex(1)}>Женщина</label>
                            </div>
                        </div>

                        

                        <div className="flex-column input-cnt relative inputs-width">
                            <label>Дата рождения</label>
                            <div className="flex">
                                <input type="text" className="day-field" placeholder="День" onChange={onPasswordChange} required />
                                <select name="month" className="month-field" defaultValue={'month'}>
                                    <option value="month" disabled>Месяц</option>
                                    <option value="jan">Январь</option>
                                    <option value="feb">Февраль</option>
                                    <option value="mar">Март</option>
                                    <option value="mar">Апрель</option>
                                    <option value="mar">Май</option>
                                    <option value="mar">Июнь</option>
                                    <option value="mar">Июль</option>
                                    <option value="mar">Август</option>
                                    <option value="mar">Сентябрь</option>
                                    <option value="mar">Октябрь</option>
                                    <option value="mar">Ноябрь</option>
                                    <option value="mar">Декабрь</option>
                                </select>
                                <input type="text" id="year" className="year-field" placeholder="Год" onChange={onPasswordChange} required />
                            </div>
                        </div>

                        <div className="flex-column input-cnt relative inputs-width">
                            <label>Цель изучения английского</label>
                            <select name="month" defaultValue={'target'}>
                                <option value="target" disabled>Выберите вашу цель</option>
                                <option value="jan">Работа</option>
                                <option value="feb">Путешествия</option>
                                <option value="mar">Досуг</option>
                            </select>
                            {/* <input type="date" id="start" name="trip-start" value="2018-07-22" min="1918-01-01" max="2018-12-31" /> */}
                        </div>

                        <div className="inputs-width flex">
                            <div className="profile-pic">
                            </div>
                            <div style={{width: 225}}>
                                <div className="change-pic border-radius text-centered pointer">Изменить фотографию</div>
                                <div className="mrg-bot">На фотографии должны быть вы</div>
                                <div className="mrg-bot">Файлы с расширением jpg, jpeg, png</div>
                                <div className="mrg-bot">Максимальный размер файла 1Мб</div>
                            </div>
                        </div>

                        <Link to={ `/user?id=${inputValues.login}` }>
                            <button className="border-radius white pointer grad-bg inputs-width">Завершить</button>
                        </Link>
                        <p className="form-inputs-width line-height text-centered inputs-width" style={{marginBottom: '1em'}}>Нажимая на кнопку, вы даете согласие на обработку персональных данных и соглашаетесь c <Link to="/privacy">политикой конфиденциальности</Link></p>

                    </div> : <div className="hide" />
                }
            </div>
        </div>
    );
}

export default Signup;